<template>
    <div class="brightness">
        <navigate title="亮度设置" position="absolute"/>
        <div class="scroll-wrapper">
            <mu-list>
                <mu-list-item class="item title border-1px-b">
                    <mu-list-item-title>屏幕亮度</mu-list-item-title>
                    <mu-list-item-action>
                        <mu-button @click="defaultBrightness" :if="tip" color="info" flat small>跟随系统</mu-button>
                    </mu-list-item-action>
                </mu-list-item>
                <mu-list-item>
                    <mu-slider style="margin: 0" :value="brightness" @change="change" :step="1"/>
                </mu-list-item>
            </mu-list>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../../base/navigate/navigate"
    import Cordova from '../../../common/js/cordova'
    import {appMixins} from "../../../common/mixins"

    export default {
        name: "brightness",
        mixins: [appMixins],
        data() {
            return {
                tip: false
            }
        },
        created() {
            Cordova.brightness.get().then(brightness => {
                console.log(`%c当前屏幕亮度为 ${brightness}`, 'color: #673ab7')
                if (brightness > 0) {
                    this.tip = true

                    this.setBrightness(Math.round(brightness * 100))
                }
            })
        },
        methods: {
            change(value) {
                Cordova.brightness.set(value).then(status => {
                    this.setBrightness(value)
                })
            },
            defaultBrightness() {
                Cordova.brightness.set(-1).then(status => {
                    this.tip = false

                    this.setBrightness(0)
                })
            }
        },
        components: {
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../../common/stylus/variable.styl"

    .brightness
        padding-top 46px

        > .scroll-wrapper
            width 100%
            height 100%
</style>



